t to do, please contact your administrator before you continue. Do you really want to continue?') }}\r\n\t\t\t\t</p>\r\n\t\t\t\t<input id=\"encrypted-continue\"\r\n\t\t\t\t\tv-model=\"proceed\"\r\n\t\t\t\t\ttype=\"checkbox\"\r\n\t\t\t\t\tclass=\"checkbox\">\r\n\t\t\t\t<label for=\"encrypted-continue\">\r\n\t\t\t\t\t{{ t('core', 'I know what I\\'m doing') }}\r\n\t\t\t\t</label>\r\n\t\t\t</div>\r\n\r\n\t\t\t<LoginButton :loading=\"loading\"\r\n\t\t\t\t:value=\"t('core', 'Reset password')\"\r\n\t\t\t\t:value-loading=\"t('core', 'Resetting password')\" />\r\n\r\n\t\t\t<p v-if=\"error && message\" :class=\"{warning: error}\">\r\n\t\t\t\t{{ message }}\r\n\t\t\t</p>\r\n\t\t</fieldset>\r\n\t</form>\r\n</template>\r\n\r\n<script>\r\nimport Axios from '@nextcloud/axios'\r\nimport LoginButton from './LoginButton.vue'\r\n\r\nexport default {\r\n\tname: 'UpdatePassword',\r\n\tcomponents: {\r\n\t\tLoginButton,\r\n\t},\r\n\tprops: {\r\n\t\tusername: {\r\n\t\t\ttype: String,\r\n\t\t\trequired: true,\r\n\t\t},\r\n\t\tresetPasswordTarget: {\r\n\t\t\ttype: String,\r\n\t\t\trequired: true,\r\n\t\t},\r\n\t},\r\n\tdata() {\r\n\t\treturn {\r\n\t\t\terror: false,\r\n\t\t\tloading: false,\r\n\t\t\tmessage: undefined,\r\n\t\t\tuser: this.username,\r\n\t\t\tpassword: '',\r\n\t\t\tencrypted: false,\r\n\t\t\tproceed: false,\r\n\t\t}\r\n\t},\r\n\twatch: {\r\n\t\tusername(value) {\r\n\t\t\tthis.user = value\r\n\t\t},\r\n\t},\r\n\tmethods: {\r\n\t\tasync submit() {\r\n\t\t\tthis.loading = true\r\n\t\t\tthis.error = false\r\n\t\t\tthis.message = ''\r\n\r\n\t\t\ttry {\r\n\t\t\t\tconst { data } = await Axios.post(this.resetPasswordTarget, {\r\n\t\t\t\t\tpassword: this.password,\r\n\t\t\t\t\tproceed: this.proceed,\r\n\t\t\t\t})\r\n\t\t\t\tif (data && data.status === 'success') {\r\n\t\t\t\t\tthis.message = 'send-success'\r\n\t\t\t\t\tthis.$emit('update:username', this.user)\r\n\t\t\t\t\tthis.$emit('done')\r\n\t\t\t\t} else if (data && data.encryption) {\r\n\t\t\t\t\tthis.encrypted = true\r\n\t\t\t\t} else if (data && data.msg) {\r\n\t\t\t\t\tthrow new Error(data.msg)\r\n\t\t\t\t} else {\r\n\t\t\t\t\tthrow new Error()\r\n\t\t\t\t}\r\n\t\t\t} catch (e) {\r\n\t\t\t\tthis.error = true\r\n\t\t\t\tthis.message = e.message ? e.message : t('core', 'Password cannot be changed. Please contact your administrator.')\r\n\t\t\t} finally {\r\n\t\t\t\tthis.loading = false\r\n\t\t\t}\r\n\t\t},\r\n\t},\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n\tfieldset {\r\n\t\ttext-align: center;\r\n\t}\r\n\r\n\tinput[type=submit] {\r\n\t\tmargin-top: 20px;\r\n\t}\r\n</style>\r\n"],"sourceRoot":""}]);
